// App.js

import React, { useState, useEffect } from 'react';
import axios from 'axios';
import DatePicker from 'react-datepicker';
import {
  LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer
} from 'recharts';
import {
  Bell, DollarSign, TrendingUp, BarChart2, Calendar, Repeat, AlertTriangle
} from 'lucide-react';
import 'react-datepicker/dist/react-datepicker.css';
import './App.css'; // Custom CSS for additional styling

function App() {
  const [ethData, setEthData] = useState(null);
  const [historicalData, setHistoricalData] = useState([]);
  const [loading, setLoading] = useState(true);
  const [alertPrice, setAlertPrice] = useState('');
  const [showAlert, setShowAlert] = useState(false);
  const [startDate, setStartDate] = useState(new Date(new Date().setDate(new Date().getDate() - 30)));
  const [endDate, setEndDate] = useState(new Date());
  const [convertAmount, setConvertAmount] = useState('');
  const [convertedAmount, setConvertedAmount] = useState(null);
  const [conversionType, setConversionType] = useState('usdToEth');
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchData();
    // Set up daily updates
    const dailyUpdate = setInterval(fetchData, 24 * 60 * 60 * 1000); // 24 hours in milliseconds
    return () => clearInterval(dailyUpdate);
  }, [startDate, endDate]);

  const fetchData = async () => {
    setLoading(true);
    try {
      // Fetch current Ethereum data
      const currentDataResponse = await axios.get('https://api.coingecko.com/api/v3/coins/ethereum');
      setEthData(currentDataResponse.data);

      // Fetch historical data
      const from = Math.floor(startDate.getTime() / 1000);
      const to = Math.floor(endDate.getTime() / 1000);

      const historicalDataResponse = await axios.get(
        `https://api.coingecko.com/api/v3/coins/ethereum/market_chart/range`,
        {
          params: {
            vs_currency: 'usd',
            from: from,
            to: to,
          },
        }
      );

      const formattedHistoricalData = historicalDataResponse.data.prices.map(([timestamp, price]) => ({
        date: new Date(timestamp).toLocaleDateString(),
        price: price,
      }));

      setHistoricalData(formattedHistoricalData);
      setLoading(false);
      setError(null);
    } catch (error) {
      console.error('Error fetching data:', error);
      setLoading(false);
      setError('Failed to fetch data. Please try again later.');
    }
  };

  useEffect(() => {
    if (
      ethData &&
      alertPrice &&
      ethData.market_data.current_price.usd >= parseFloat(alertPrice)
    ) {
      setShowAlert(true);
    }
  }, [ethData, alertPrice]);

  const handleSetAlert = () => {
    setAlertPrice(parseFloat(alertPrice));
    setShowAlert(false);
  };

  const handleConvert = () => {
    if (ethData) {
      let converted;
      if (conversionType === 'usdToEth') {
        converted = parseFloat(convertAmount) / ethData.market_data.current_price.usd;
      } else {
        converted = parseFloat(convertAmount) * ethData.market_data.current_price.usd;
      }
      setConvertedAmount(converted);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-screen bg-gradient-to-r from-purple-900 via-gray-900 to-blue-900">
        <div className="text-white text-2xl animate-pulse">Loading Ethereum Data...</div>
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-900 text-white">
      {/* Header */}
      <header className="p-6 bg-gray-800 flex justify-between items-center">
        <h1 className="text-3xl font-bold">Ethereum Tracker</h1>
        <nav>
          <a href="#dashboard" className="mr-4 hover:text-gray-400">Dashboard</a>
          <a href="#history" className="mr-4 hover:text-gray-400">History</a>
          <a href="#converter" className="hover:text-gray-400">Converter</a>
        </nav>
      </header>

      {/* Alert Notification */}
      {showAlert && (
        <div className="bg-yellow-500 text-black p-4 flex items-center justify-between">
          <div className="flex items-center">
            <AlertTriangle className="mr-2" />
            <span>Ethereum price has reached ${alertPrice}!</span>
          </div>
          <button onClick={() => setShowAlert(false)} className="font-bold">Dismiss</button>
        </div>
      )}

      {/* Error Message */}
      {error && (
        <div className="bg-red-500 text-white p-4 flex items-center justify-between">
          <div className="flex items-center">
            <AlertTriangle className="mr-2" />
            <span>{error}</span>
          </div>
          <button onClick={() => setError(null)} className="font-bold">Dismiss</button>
        </div>
      )}

      {/* Dashboard */}
      <section id="dashboard" className="p-6">
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          {/* Current Price */}
          <div className="bg-gray-800 p-4 rounded-lg hover:shadow-lg transition-shadow">
            <div className="flex items-center mb-4">
              <DollarSign className="text-green-400 mr-2" />
              <h2 className="text-xl font-semibold">Current Price</h2>
            </div>
            <p className="text-3xl font-bold">${ethData.market_data.current_price.usd.toFixed(2)}</p>
          </div>

          {/* 24h Change */}
          <div className="bg-gray-800 p-4 rounded-lg hover:shadow-lg transition-shadow">
            <div className="flex items-center mb-4">
              <TrendingUp className="text-blue-400 mr-2" />
              <h2 className="text-xl font-semibold">24h Change</h2>
            </div>
            <p
              className={`text-3xl font-bold ${
                ethData.market_data.price_change_percentage_24h >= 0 ? 'text-green-500' : 'text-red-500'
              }`}
            >
              {ethData.market_data.price_change_percentage_24h.toFixed(2)}%
            </p>
          </div>

          {/* Market Cap */}
          <div className="bg-gray-800 p-4 rounded-lg hover:shadow-lg transition-shadow">
            <div className="flex items-center mb-4">
              <BarChart2 className="text-yellow-400 mr-2" />
              <h2 className="text-xl font-semibold">Market Cap</h2>
            </div>
            <p className="text-3xl font-bold">${ethData.market_data.market_cap.usd.toLocaleString()}</p>
          </div>

          {/* Set Price Alert */}
          <div className="bg-gray-800 p-4 rounded-lg hover:shadow-lg transition-shadow">
            <div className="flex items-center mb-4">
              <Bell className="text-red-400 mr-2" />
              <h2 className="text-xl font-semibold">Set Price Alert</h2>
            </div>
            <div className="flex items-center">
              <input
                type="number"
                placeholder="Alert price"
                value={alertPrice}
                onChange={(e) => setAlertPrice(e.target.value)}
                className="p-2 rounded bg-gray-700 border border-gray-600 mr-2 w-full"
              />
              <button
                onClick={handleSetAlert}
                className="bg-red-500 hover:bg-red-600 text-white font-bold py-2 px-4 rounded"
              >
                Set Alert
              </button>
            </div>
          </div>
        </div>
      </section>

      {/* Historical Data */}
      <section id="history" className="p-6 bg-gray-800">
        <div className="flex flex-col md:flex-row md:items-center md:justify-between mb-6">
          <h2 className="text-2xl font-bold mb-4 md:mb-0">Historical Price Chart</h2>
          <div className="flex items-center">
            <Calendar className="text-gray-400 mr-2" />
            <DatePicker
              selected={startDate}
              onChange={(date) => setStartDate(date)}
              selectsStart
              startDate={startDate}
              endDate={endDate}
              className="p-2 rounded bg-gray-700 border border-gray-600 text-white mr-2"
            />
            <span className="text-gray-400 mr-2">to</span>
            <DatePicker
              selected={endDate}
              onChange={(date) => setEndDate(date)}
              selectsEnd
              startDate={startDate}
              endDate={endDate}
              minDate={startDate}
              className="p-2 rounded bg-gray-700 border border-gray-600 text-white mr-2"
            />
            <button
              onClick={fetchData}
              className="bg-blue-500 hover:bg-blue-600 text-white font-bold py-2 px-4 rounded flex items-center"
            >
              <Repeat className="mr-2" />
              Update
            </button>
          </div>
        </div>
        <ResponsiveContainer width="100%" height={400}>
          <LineChart data={historicalData}>
            <CartesianGrid strokeDasharray="3 3" stroke="#444" />
            <XAxis dataKey="date" stroke="#888" />
            <YAxis stroke="#888" />
            <Tooltip contentStyle={{ backgroundColor: '#333', border: 'none' }} />
            <Line
              type="monotone"
              dataKey="price"
              stroke="#82ca9d"
              strokeWidth={2}
              dot={false}
              activeDot={{ r: 6 }}
            />
          </LineChart>
        </ResponsiveContainer>
      </section>

      {/* Converter */}
      <section id="converter" className="p-6">
        <div className="bg-gray-800 p-6 rounded-lg">
          <h2 className="text-2xl font-bold mb-4">Currency Converter</h2>
          <div className="flex flex-col md:flex-row md:items-center">
            <div className="flex items-center mb-4 md:mb-0 md:mr-4">
              <input
                type="number"
                placeholder="Amount"
                value={convertAmount}
                onChange={(e) => setConvertAmount(e.target.value)}
                className="p-2 rounded bg-gray-700 border border-gray-600 mr-2 w-full"
              />
              <select
                value={conversionType}
                onChange={(e) => setConversionType(e.target.value)}
                className="p-2 rounded bg-gray-700 border border-gray-600 text-white"
              >
                <option value="usdToEth">USD to ETH</option>
                <option value="ethToUsd">ETH to USD</option>
              </select>
            </div>
            <button
              onClick={handleConvert}
              className="bg-green-500 hover:bg-green-600 text-white font-bold py-2 px-4 rounded"
            >
              Convert
            </button>
          </div>
          {convertedAmount !== null && (
            <p className="text-xl mt-4">
              {conversionType === 'usdToEth'
                ? `$${convertAmount} USD = ${convertedAmount.toFixed(6)} ETH`
                : `${convertAmount} ETH = $${convertedAmount.toFixed(2)} USD`}
            </p>
          )}
        </div>
      </section>

      {/* Footer */}
      <footer className="p-6 bg-gray-800 text-center text-gray-400">
         {new Date().getFullYear()} Ethereum Tracker. All rights reserved.
      </footer>
    </div>
  );
}

export default App;
